  .landing-content h1 {
    position: relative;
    font-weight: 600;
    font-size: calc(1em + 2vw);
    color: #070D58; }
  .landing-content p {
    width: 80%;
    position: relative;
    font-family: Lato;
    font-size: calc(.5em + 1vw);
    margin-top: 1rem;
    line-height: 3rem;
    color: #2B2B2B; }

.bgImage {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100vh;
  width: auto;
  margin-right: -70px;
  z-index: -1; }

.search-form {
  display: relative;
  margin-top: calc(2em + 2vw); }

.city-input,
.dist-input {
  width: calc(1em + 15vw);
  height: calc(1em + 2vw);
  display: inline;
  padding-left: 5px;
  margin-right: 1rem; }

.dist-input {
  width: 8vw; }

.btn-primary {
  background-color: #070D58;
  border-color: #070D58;
  height: 50px;
  width: 125px;
  font-size: 22px;
  font-weight: bold; }

.btn-primary:hover {
  background-color: #020851;
  border-color: #020851; }

.search-form > .landing-btn {
  height: calc(1em + 1.5vw);
